* {
  box-sizing: border-box;
  font-family: sans-serif;
  margin: 0;
  padding: 0;
}

.header {
  align-items: center;
  background-color: #464954;
  display: flex;
  height: 9%;
  justify-content: center;
  list-style: none;
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;
}

.home, .about, .project, .contact, .page-not-found {
  background-color: #1FA791;
  padding-top: 70px;
  position: relative;
}

.about {
  width: 100%;
}

.about .container {
  display: flex;
  flex-flow: column;
}

.about img {
  border-radius: 30px;
  box-shadow: 5px 5px rgba(0, 0, 0, 0.4);
  max-width: 300px;
}

.about .container section {
  margin: 2% 3%;
}

.about .container section:nth-child(1) {
  align-self: flex-end;
  display: block;
}

.about .container section:nth-child(3) {
  align-self: flex-end;
  display: block;
}

.about .container section:nth-child(5) {
  align-self: flex-end;
  display: block;
}

.footer {
  overflow: hidden;
  background-color: #464954;
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 100;
}

.menu {
  align-items: center;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  list-style: none;
  padding: 15px 25px 15px 15px;
}

.menu a {
  color: #F9F9F9;
  font-size: 17px;
  padding: 15px 15px 5px;
  text-align: center;
  text-decoration: none;
}

.menu a:hover {
  background-color: #F9F9F9;
  color: #464954;
  border-radius: 25px;
  height: 35px;
}

.menu li {
  align-items: center;
  border: #1FA791 1px solid;
  border-radius: 25px;
  display: flex;
  font-weight: 900;
  justify-content: center;
  height: 35px;
  margin: 2px 5px;
}

.nave {
  margin: 0 auto;
}

.title {
  color: #F9F9F9;
  display: none;
  padding: 0 10px;
}

.title span {
  color: #1FA791;
}

.centered {
  top: 0;
  z-index: 2;
}

.left {
  left: 0;
  background-color: #F9F9F9;
}

.right {
  background-color: #12a78e;
  color: #F9F9F9;
  font-size: larger;
  padding: 6% 4%;
  right: 0;
  text-align: justify;
  text-indent: 2rem;
  width: 100%;
}

.perfil {
  display: none;
}

.split {
  align-items: center;
  background-size: contain;
  display: flex;
  flex-flow: column;
  margin: 0 auto;
  position: relative;
  width: 400px;
}

.vertical-img {
  height: 300px;
  left: 0;
  object-position: left;
  position: fixed;
  object-position: left;
  top: 0;
  z-index: 2;
  }

@media screen and (min-width: 660px) {
  .header {
    align-items: center;
    justify-content: space-between;
  }

  .menu a {
    padding: 15px 10px 15px 15px;
  }

  .title {
    display: initial;
    padding: 15px 0 15px 40px;
  }

  .home, .about, .project, .contact, .page-not-found {
    padding-top: 79px;
  }

  .perfil {
    clip-path: ellipse(50% 50% at 50% 50%);
    left: 3vh;
    max-width: 350px;
    position: fixed;
    top: 15vh;
    z-index: 1;
  }

  .profile {
    height: 40%;
    width: 100%;
  }

}

@media screen and (min-width: 900px) {
  .left {
    width: 30%;
  }
  
  .perfil {
    display: block;
  }
}